@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@0;1&family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ux-port {
  background-color: #e8e8e8;
  color: #313131;
  line-height: 1.6;
  font-size: 1.1rem;
  font-family: montserrat, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.ux-port h1, .ux-port h2, .ux-port h3, .ux-port h4, .ux-port h5, .ux-port h6,
.ux-port .h1, .ux-port .h2, .ux-port .h3, .ux-port .h4, .ux-port .h5, .ux-port .h6 {
  font-family: montserrat, sans-serif;
  font-style: normal;
  font-weight: 800;
}

.ux-port .sub-section h4, .ux-port .sub-section h5, .ux-port .sub-section h6, .ux-port .sub-section .h4, .ux-port .sub-section .h5, .ux-port .sub-section .h6 {
  text-align: end;
}

.ux-port a {
  color: #e53935;
}

.ux-port blockquote {
  font-style: oblique;
}

.ux-port .card {
  background-color: transparent;
}

.ux-port .card a:hover {
  text-decoration: none;
}

.ux-port .navbar-brand {
  font-weight: 700;
  font-size: 2rem;
  color: #313131;
}

.ux-port .navbar-brand:hover {
  color: #313131;
}

.ux-port .port-hero img {
  max-height: 300px;
}

.ux-port .port-hero, .ux-port .jumbotron, .ux-port .bg-contrast {
  background-color: #e0e2e4;
}

.ux-port .title-border {
  border-left: 3px solid #e53935;
  padding-left: 1.2rem;
}

.ux-port .port-section {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}

.ux-port .sect-row {
  padding-top: 1rem;
  padding-bottom: 2rem;
}

.ux-port .justify-content-end {
  justify-content: center !important;
}

.ux-port .justify-content-end {
  justify-content: end !important;
}
